body {
  background: url(./home_body_bg.jpg) top center repeat-x;
}
#menu ul,
#menu li,
#menu span,
#menu a {
  margin: 0;
  padding: 0;
  position: relative;
}
#menu {
  line-height: 1;
  background: none;
  color: #fff;
  font-size: 16px;
  letter-spacing: 1px;
  max-height: 59px;
  min-height: 59px;
  padding: 0 168px;
  width: auto;
  /* Flex */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
#menu > .izquierda{
  -ms-flex-align: center;
      align-items: center;
  /* Flex */
  display: -ms-flexbox;
  display: flex;

}
#menu > .derecha {
  -ms-flex-align: center;
      align-items: center;
  /* Flex */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
}
#menu .item {
  margin-left: 24px;
  cursor: pointer;
}
#menu .item:hover {
  color: #fff;
}
#menu .activo {
  -ms-flex-align: center;
      align-items: center;
  background: #3e8970;
  border: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  color: #fff;
  height: 18px;
  padding: 16px 12px;
  text-align: center;
  /* Flex */
  display: -ms-flexbox;
  display: flex;
}
#menu .activo:hover{
  background: rgba(0, 0, 0, 0.25);
}
#menu .avatar {
  border-radius: 3px;
  height: 24px;
  margin-right: 12px;
  width: 24px;
}



